body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    /* display: table; */
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .rec {
    background: rgb(1, 121, 233);
    border: 1px solid rgba(0, 0, 0, 0.336);
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: -100px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    /* text-align: center; */
    width: 10px;
    bottom: 0;
    display: flex;
    flex-direction: row;
  }
  
  .rec:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
    justify-content: center;
    position: relative;
    
  }
  
  .game-info {
    margin-left: 20px;
  }
  